  .artist-branding {
    bottom: 40px;
    left: 20px;
    right: 20px;
    flex-direction: column;
    align-items: flex-start;
    gap: 20px;
  }
  .artist-branding .slideshow-controls-container {
    align-self: center;
  }
  .artist-branding .artist-info .artist-name {
    font-size: 32px;
  }
  .artist-branding .artist-info .artist-subtitle {
    font-size: 14px;
  }
  .artist-branding .artist-info .artist-tagline {
    font-size: 12px;
  }
}
.main-footer {
  background: linear-gradient(135deg, #cc3333, #aa2222);
  color: white;
  height: 30vh;
  padding: 40px 0;
  display: flex;
  align-items: center;
  position: relative;
}
.main-footer .waved-background {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: radial-gradient(circle at 20% 50%, rgba(255, 255, 255, 0.1) 0%, transparent 50%), radial-gradient(circle at 80% 20%, rgba(255, 255, 255, 0.05) 0%, transparent 50%), radial-gradient(circle at 40% 80%, rgba(255, 255, 255, 0.08) 0%, transparent 50%);
  background-size: 100px 100px, 150px 150px, 200px 200px;
  opacity: 0.3;
  animation: wave 20s ease-in-out infinite;
}
@keyframes wave {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
}
.main-footer .footer-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 40px;
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 80px;
  align-items: start;
}
.main-footer .footer-left {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 60px;
}
.main-footer .social-section h3,
.main-footer .nav-section h3 {
  font-size: 16px;
  font-weight: 900;
  margin-bottom: 25px;
  font-family: "Courier New", monospace;
  letter-spacing: 2px;
  color: white;
}
.main-footer .social-links {
  display: flex;
  flex-direction: column;
  gap: 15px;
}
.main-footer .social-links .social-link {
  color: white;
  text-decoration: none;
  font-size: 14px;
  font-family: "Courier New", monospace;
  font-weight: 600;
  letter-spacing: 1px;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
}
.main-footer .social-links .social-link .social-icon {
  margin-right: 10px;
  width: 16px;
  height: 16px;
  fill: currentColor;
}
.main-footer .social-links .social-link:hover {
  color: #ffeeee;
  transform: translateX(5px);
}
.main-footer .nav-buttons {
  display: flex;
  flex-direction: column;
  gap: 15px;
}
.main-footer .nav-buttons .nav-button {
  background: rgba(255, 255, 255, 0.1);
  color: white;
  text-decoration: none;
  padding: 12px 20px;
  border-radius: 4px;
  font-family: "Courier New", monospace;
  font-weight: 600;
  font-size: 14px;
  letter-spacing: 1px;
  transition: all 0.3s ease;
  text-align: center;
  border: 2px solid transparent;
}
.main-footer .nav-buttons .nav-button:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: translateY(-2px);
}
.main-footer .nav-buttons .nav-button.active {
  background: rgba(255, 255, 255, 0.3);
  border-color: rgba(255, 255, 255, 0.5);
}
.main-footer .footer-right {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  gap: 20px;
}
.main-footer .footer-right .scanning-svg .scanning-image {
  width: 120px;
  height: auto;
  filter: invert(1) brightness(0.9);
  opacity: 0.8;
}
.main-footer .footer-right .footer-logo {
  text-align: right;
}
.main-footer .footer-right .footer-logo .logo-text {
  font-size: 32px;
  font-weight: 900;
  font-family: "Arial", sans-serif;
  letter-spacing: 3px;
  margin-bottom: 5px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}
.main-footer .footer-right .footer-logo .logo-tagline {
  font-size: 14px;
  font-weight: 600;
  font-family: "Courier New", monospace;
  letter-spacing: 2px;
  opacity: 0.9;
}

@media (max-width: 768px) {
  .main-footer {
    height: auto;
    min-height: 30vh;
    padding: 30px 0;
  }
  .main-footer .footer-container {
    grid-template-columns: 1fr;
    gap: 40px;
    padding: 0 20px;
  }
  .main-footer .footer-left {
    grid-template-columns: 1fr;
    gap: 40px;
  }
  .main-footer .footer-right {
    justify-content: center;
  }
  .main-footer .footer-right .footer-logo {
    text-align: center;
  }
  .main-footer .footer-right .footer-logo .logo-text {
    font-size: 24px;
    letter-spacing: 2px;
  }
  .main-footer .footer-right .footer-logo .logo-tagline {
    font-size: 12px;
  }
}
@media (max-width: 480px) {
  .main-footer .social-links .social-link {
    font-size: 12px;
  }
  .main-footer .social-links .social-link .social-icon {
    font-size: 14px;
  }
  .main-footer .nav-buttons .nav-button {
    padding: 10px 15px;
    font-size: 12px;
  }
}
.artist-branding {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 15;
  align-items: flex-start;
}
.artist-branding .artist-branding-block {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  max-width: 1200px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 0 40px;
}
.artist-branding .bottom-top-gradient-shadow {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 200px;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0.7) 100%);
  mix-blend-mode: multiply;
  opacity: 0.7;
  pointer-events: none;
  z-index: -1;
}
.artist-branding__logo {
  align-self: center;
}
.artist-branding__logo-icon {
  height: 110px;
  filter: invert(1);
}
.artist-branding__content {
  color: white;
}
.artist-branding__name {
  font-size: 3rem;
  font-weight: 300;
  font-family: "Andvari";
  letter-spacing: 0.1em;
  margin: 0;
  color: white;
  text-transform: uppercase;
}
.artist-branding__subtitle {
  font-size: 1.2rem;
  font-weight: 300;
  margin: 0;
  color: rgba(255, 255, 255, 0.8);
  letter-spacing: 0.05em;
}
.artist-branding__separator {
  width: 100%;
  height: 1px;
  background: rgba(255, 255, 255, 0.3);
}
.artist-branding__tagline-wrapper {
  align-items: center;
  width: 212px;
  justify-content: center;
}
.artist-branding__tagline {
  font-size: 1.1rem;
  font-weight: 300;
  color: rgba(255, 255, 255, 0.8);
  margin: 0.5rem 0 0 0;
  letter-spacing: 0.05em;
}
@media (max-width: 1024px) {
  .artist-branding {
    padding: 1.5rem 2rem;
    bottom: 2rem;
    gap: 2rem;
  }
  .artist-branding__name {
    font-size: 2.5rem;
  }
  .artist-branding__logo-icon {
    width: 60px;
    height: 60px;
  }
}
@media (max-width: 768px) {
  .artist-branding {
    grid-template-columns: 1fr;
    gap: 1.5rem;
    padding: 1rem;
    bottom: 1rem;
  }
  .artist-branding__logo {
    justify-self: center;
  }
  .artist-branding__content {
    text-align: center;
  }
  .artist-branding__row {
    flex-direction: column;
    gap: 1rem;
  }
  .artist-branding__name {
    font-size: 2rem;
  }
  .artist-branding__logo-icon {
    width: 50px;
    height: 50px;
  }
